import React, { useState, useEffect } from 'react';

//  - base64
const PLATFORM_ICONS = {
  claude: {
    sources: [
      'https://www.anthropic.com/favicon.ico',
      'https://claude.ai/favicon.ico',
      'https://www.google.com/s2/favicons?sz=32&domain=claude.ai'
    ],
    // Claudebase64
    base64: 'data:image/svg+xml;base64,'
  },
  gemini: {
    sources: [
      'https://ssl.gstatic.com/lamda/images/gemini_android_icon_24dp_9b8cf66e9d9eaacae59aa96df9e0f63b2c8bd50b.png',
      'https://www.gstatic.com/lamda/images/gemini_sparkle_red_4ed1cbfcbc6c9e84c31b987da73fc4168aec8445.svg',
      'https://api.allorigins.win/raw?url=' + encodeURIComponent('https://www.google.com/s2/favicons?sz=32&domain=gemini.google.com')
    ],
    fallback: '',
    // Geminibase64
    base64: 'data:image/svg+xml;base64,'
  },
  notebooklm: {
    sources: [
      'https://api.allorigins.win/raw?url=' + encodeURIComponent('https://www.google.com/s2/favicons?sz=32&domain=notebooklm.google')
    ],
    fallback: '',
    base64: 'data:image/svg+xml;base64,'
  }
};

// 
const CACHE_VERSION = 'v4'; // 

// 
const DEBUG_MODE = false; // 

// 
window.clearPlatformIconCache = () => {
  let clearedCount = 0;
  for (let i = localStorage.length - 1; i >= 0; i--) {
    const key = localStorage.key(i);
    if (key && key.startsWith('platform_icon_')) {
      localStorage.removeItem(key);
      clearedCount++;
    }
  }
  console.log(`%c[PlatformIcon]  ${clearedCount} `, 'color: #1A73E8; font-weight: bold');
  console.log(`%c[PlatformIcon]  2 ...`, 'color: #4B5563');
  // 
  setTimeout(() => {
    window.location.reload();
  }, 2000);
};

// 
window.showPlatformIconStatus = () => {
  const iconKeys = ['claude', 'gemini', 'notebooklm'];
  const currentVersion = CACHE_VERSION;
  
  console.log(`%c[PlatformIcon] : ${currentVersion}`, 'color: #1A73E8; font-weight: bold');
  console.log('%c[PlatformIcon] :', 'color: #1A73E8; font-weight: bold');
  
  iconKeys.forEach(iconKey => {
    const cacheKey = `platform_icon_${iconKey}_${currentVersion}_16`;
    const cached = localStorage.getItem(cacheKey);
    
    let status = ' ';
    let color = '#666';
    
    if (cached === 'use_base64') {
      status = ' Base64';
      color = '#1A73E8';
    } else if (cached === 'failed') {
      status = ' ';
      color = '#DC2626';
    } else if (cached && cached.startsWith('data:image')) {
      status = ' ';
      color = '#059669';
    }
    
    console.log(`%c  ${iconKey}: ${status}`, `color: ${color}`);
  });
  
  console.log('%c:', 'color: #1A73E8; font-weight: bold');
  console.log('%c  clearPlatformIconCache() - ', 'color: #666');
  console.log('%c  showPlatformIconStatus() - ', 'color: #666');
};

const PlatformIcon = ({ platform, format, size = 16, style = {} }) => {
  const [iconSrc, setIconSrc] = useState(null);
  const [hasError, setHasError] = useState(false);

  // formatplatform
  const getIconKey = () => {
    if (format === 'gemini_notebooklm') {
      if (platform === 'notebooklm') return 'notebooklm';
      //  Gemini 
      if (platform === 'gemini' || platform === 'aistudio' || platform === 'google ai studio') {
        return 'gemini';
      }
      return 'gemini'; // gemini
    }
    return 'claude';
  };

  const iconKey = getIconKey();
  const iconConfig = PLATFORM_ICONS[iconKey];

  useEffect(() => {
    // key
    const cacheKey = `platform_icon_${iconKey}_${CACHE_VERSION}_${size}`;
    const cached = localStorage.getItem(cacheKey);
    
    if (DEBUG_MODE) {
      console.log(`[PlatformIcon] : platform=${platform}, format=${format}, iconKey=${iconKey}`);
    }
    
    // 
    if (cached && cached !== 'failed' && (cached.startsWith('data:image') || cached === 'use_base64')) {
      if (cached === 'use_base64') {
        // base64
        setIconSrc(iconConfig.base64);
        setHasError(false);
        if (DEBUG_MODE) console.log(`[PlatformIcon] base64: ${iconKey}`);
      } else {
        // 
        setIconSrc(cached);
        setHasError(false);
        if (DEBUG_MODE) console.log(`[PlatformIcon] : ${iconKey}`);
      }
      return;
    }
    
    // base64
    if (cached === 'failed') {
      setIconSrc(iconConfig.base64);
      setHasError(false);
      if (DEBUG_MODE) console.log(`[PlatformIcon] base64: ${iconKey}`);
      return;
    }

    // 
    for (let i = localStorage.length - 1; i >= 0; i--) {
      const key = localStorage.key(i);
      if (key && key.startsWith(`platform_icon_${iconKey}_`) && !key.includes(CACHE_VERSION)) {
        localStorage.removeItem(key);
      }
    }

    // 
    setIconSrc(null);
    setHasError(false);

    // 
    const tryLoadIcon = async () => {
      if (DEBUG_MODE) console.log(`[PlatformIcon] : ${iconKey}`);
      
      // 
      for (let i = 0; i < iconConfig.sources.length; i++) {
        const url = iconConfig.sources[i];
        if (DEBUG_MODE) console.log(`[PlatformIcon]  ${i + 1}/${iconConfig.sources.length}: ${url}`);
        
        try {
          const success = await tryLoadFromUrl(url, cacheKey, iconKey);
          if (success) {
            if (DEBUG_MODE) console.log(`[PlatformIcon] : ${iconKey} from ${url}`);
            return;
          }
        } catch (error) {
          if (DEBUG_MODE) console.warn(`[PlatformIcon]  ${i + 1} :`, error);
          continue;
        }
      }
      
      // base64
      if (DEBUG_MODE) console.log(`[PlatformIcon] base64: ${iconKey}`);
      setIconSrc(iconConfig.base64);
      setHasError(false);
      localStorage.setItem(cacheKey, 'use_base64');
    };

    tryLoadIcon();
  }, [iconKey, iconConfig, size]);

  // URL
  const tryLoadFromUrl = (url, cacheKey, iconKey) => {
    return new Promise((resolve) => {
      const img = new Image();
      img.crossOrigin = 'anonymous';
      
      // 
      const timeoutId = setTimeout(() => {
        resolve(false);
      }, 3000); // 3
      
      img.onload = () => {
        clearTimeout(timeoutId);
        
        // base64
        const canvas = document.createElement('canvas');
        canvas.width = 32;
        canvas.height = 32;
        const ctx = canvas.getContext('2d');
        
        try {
          ctx.drawImage(img, 0, 0, 32, 32);
          const dataUrl = canvas.toDataURL('image/png');
          setIconSrc(dataUrl);
          localStorage.setItem(cacheKey, dataUrl);
          setHasError(false);
          resolve(true);
        } catch (error) {
          resolve(false);
        }
      };
      
      img.onerror = () => {
        clearTimeout(timeoutId);
        resolve(false);
      };
      
      img.src = url;
    });
  };

  // fallback
  if (!iconSrc || hasError) {
    return (
      <span 
        style={{ 
          fontSize: size,
          lineHeight: 1,
          display: 'inline-block',
          verticalAlign: 'middle',
          ...style 
        }}
      >
        {iconConfig.fallback}
      </span>
    );
  }

  return (
    <img 
      src={iconSrc}
      alt={iconKey}
      style={{ 
        width: size, 
        height: size,
        display: 'inline-block',
        verticalAlign: 'middle',
        borderRadius: '2px', // 
        ...style 
      }}
      onError={() => {
        // base64
        if (iconSrc !== iconConfig.base64) {
          setIconSrc(iconConfig.base64);
        } else {
          // base64emoji
          setHasError(true);
        }
      }}
    />
  );
};

export default PlatformIcon;