// components/ConversationFilter.js
import React from 'react';

const ConversationFilter = ({
  filters,
  availableProjects,
  filterStats,
  onFilterChange,
  onReset,
  className = ""
}) => {
  const formatDate = (dateStr) => {
    if (!dateStr) return '';
    try {
      return new Date(dateStr).toISOString().split('T')[0];
    } catch {
      return '';
    }
  };

  return (
    <div className={`conversation-filter ${className}`}>
      {/*  */}
      <div className="filter-panel">
        {/*  */}
        <div className="filter-header">
          <div className="filter-title">
            <span className="filter-icon"></span>
            <span className="filter-text"></span>
            {filterStats.hasActiveFilters && (
              <span className="filter-badge">{filterStats.activeFilterCount}</span>
            )}
          </div>
          {filterStats.hasActiveFilters && (
            <button 
              className="btn-secondary small"
              onClick={onReset}
              title=""
            >
               
            </button>
          )}
        </div>

        <div className="filter-sections">
          {/*  */}
          <div className="filter-section">
            <label className="filter-label"></label>
            <input
              type="text"
              className="filter-input"
              placeholder="..."
              value={filters.name}
              onChange={(e) => onFilterChange('name', e.target.value)}
            />
          </div>

          {/*  */}
          <div className="filter-section">
            <label className="filter-label"></label>
            <select
              className="filter-select"
              value={filters.dateRange}
              onChange={(e) => onFilterChange('dateRange', e.target.value)}
            >
              <option value="all"></option>
              <option value="today"></option>
              <option value="week"></option>
              <option value="month"></option>
              <option value="custom"></option>
            </select>
          </div>

          {/*  */}
          {filters.dateRange === 'custom' && (
            <div className="filter-section date-range-section">
              <label className="filter-label"></label>
              <div className="date-range-inputs">
                <input
                  type="date"
                  className="filter-input date-input"
                  value={formatDate(filters.customDateStart)}
                  onChange={(e) => onFilterChange('customDateStart', e.target.value)}
                  title=""
                />
                <span className="date-separator"></span>
                <input
                  type="date"
                  className="filter-input date-input"  
                  value={formatDate(filters.customDateEnd)}
                  onChange={(e) => onFilterChange('customDateEnd', e.target.value)}
                  title=""
                />
              </div>
            </div>
          )}

          {/*  */}
          <div className="filter-section">
            <label className="filter-label"></label>
            <select
              className="filter-select"
              value={filters.project}
              onChange={(e) => onFilterChange('project', e.target.value)}
            >
              <option value="all"></option>
              <option value="no_project"> </option>
              {availableProjects.map(project => (
                <option key={project.uuid} value={project.uuid}>
                   {project.name}
                </option>
              ))}
            </select>
          </div>

          {/*  */}
          <div className="filter-section">
            <label className="filter-label"></label>
            <select
              className="filter-select"
              value={filters.starred}
              onChange={(e) => onFilterChange('starred', e.target.value)}
            >
              <option value="all"></option>
              <option value="starred"> </option>
              <option value="unstarred"> </option>
            </select>
          </div>
        </div>

        {/*  */}
        <div className="filter-footer">
          <div className="filter-stats">
            <span className="stats-text">
               <strong>{filterStats.filtered}</strong> / {filterStats.total} 
            </span>
            {filterStats.hasActiveFilters && (
              <span className="active-filters-text">
                {filterStats.activeFilterCount} 
              </span>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default ConversationFilter;